<template>
    <div class="yd-lightbox-tip" v-if="show">
        <img src="data:image/gif;base64,">
    </div>
</template>

<script type="text/babel">
    export default {
        data() {
            return {
                show: window.localStorage.getItem('LIGHTBOX-TIP') != 1
            }
        },
        mounted() {
            if (this.show) {
                setTimeout(() => {
                    this.show = false;
                    window.localStorage.setItem('LIGHTBOX-TIP', 1);
                }, 3500);
            }
        }
    }
</script>



